.checkbox input[type="checkbox"] {
    display: none
   }
.checkbox{
    display: flex;
    flex-wrap: wrap;
}
.checkbox label::before{
    content: "";
    background: url('../img/icon/outtter.png');
    display: inline-block;
    height: 20px;
    width: 20px;
    cursor: pointer;
   }
   .checkbox label::after {
    content: "";
    margin-top:-14px;
    margin-left:-1px;
    cursor: pointer;
   }
   .checkbox label {
    position: relative;
   }
   .checkbox label::before,
   .checkbox label::after {
    position: absolute;
   }
   /*Внешний блок*/
   .checkbox label::before {
    top: 3px;
   }
   /*Галка*/
   .checkbox label::after {
    left: 5px;
    top: 17px;
   }
   /*Прячем галку по умолчанию*/
.checkbox input[type="checkbox"] + label::after {
    content: none;
   }
   /*Показываем галку по состоянию checked*/
   .checkbox input[type="checkbox"]:checked + label::after {
    content: url('../img/icon/arrteeem.png');
   }
